<template>
  <div style="margin: 20px" align="center" justify="center">
    <v-card
      width="60%"
      style="border: 3px solid #13314a; border-radius: 20px; background-color: #f2fcff !important;"
      class="elevation-15"
    >
      <h1 class="title" style="margin:30px">
        ELECCIÓN DE COLORES
      </h1>
      <table class="table" style="width:60%">
        <thead>
          <tr>
            <th class="text-center">
              OCIO
            </th>
            <th class="text-center">
              SERVICIOS
            </th>
            <th class="text-center">
              COMPRAS
            </th>
            <th class="text-center">
              OTRO
            </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td class="text-center">
              <v-swatches
                v-model="colorOcio"
                :swatches="colors"
                show-fallback
                fallback-input-type="color"
              ></v-swatches>
            </td>
            <td class="text-center">
              <v-swatches
                v-model="colorServicios"
                :swatches="colors"
                show-fallback
                fallback-input-type="color"
              ></v-swatches>
            </td>
            <td class="text-center">
              <v-swatches
                v-model="colorCompras"
                :swatches="colors"
                show-fallback
                fallback-input-type="color"
              ></v-swatches>
            </td>
            <td class="text-center">
              <v-swatches
                v-model="colorOtro"
                :swatches="colors"
                show-fallback
                fallback-input-type="color"
              ></v-swatches>
            </td>
          </tr>
        </tbody>
      </table>

      <table class="table2 mt-4 ">
        <tbody>
          <tr>
            <td class="blindText" @click="setDefault" style="cursor: pointer; ">
              Predeterminado
            </td>
            <td>
              <v-swatches :value="colorsDefault[0]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsDefault[1]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsDefault[2]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsDefault[3]" disabled></v-swatches>
            </td>
            <td
              class="blindText"
              @click="setDeut"
              style="cursor: pointer;padding-left:50px !important"
            >
              Deuteranopia
            </td>
            <td>
              <v-swatches :value="colorsDeut[0]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsDeut[1]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsDeut[2]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsDeut[3]" disabled></v-swatches>
            </td>
          </tr>
          <tr>
            <td class="blindText" @click="setProt" style="cursor: pointer;">
              Protanopia
            </td>
            <td>
              <v-swatches :value="colorsProt[0]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsProt[1]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsProt[2]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsProt[3]" disabled></v-swatches>
            </td>
            <td
              class="blindText"
              @click="setTrit"
              style="cursor: pointer;padding-left:50px !important"
            >
              Tritanopia
            </td>
            <td>
              <v-swatches :value="colorsTrit[0]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsTrit[1]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsTrit[2]" disabled></v-swatches>
            </td>
            <td>
              <v-swatches :value="colorsTrit[3]" disabled></v-swatches>
            </td>
          </tr>
        </tbody>
      </table>

      <v-divider width="90%" class="mt-5 mb-5"></v-divider>
      <v-container>
        <v-row align="center" justify="center">
          <h1 class="title" style="margin-bottom:20px">
            EDITAR PERFIL
          </h1>
        </v-row>
        <v-row>
          <v-col
            ><v-text-field
              class="icon left"
              id="name"
              v-model="nameTf"
              label="Nombre"
              name="name"
              append-icon="mdi-account-outline"
              type="text"
              required
              :error-messages="nameErrors"
              @input="$v.nameTf.$touch()"
              @blur="$v.nameTf.$touch()"
            />
          </v-col>
          <v-col
            ><v-text-field
              class="icon right"
              id="username"
              v-model="usernameTf"
              label="Nombre de usuario"
              name="username"
              append-icon="mdi-smart-card-outline"
              type="text"
              required
              :error-messages="usernameErrors"
              @input="$v.usernameTf.$touch()"
              @blur="$v.usernameTf.$touch()"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col
            ><v-text-field
              class="icon left"
              id="emailRegister"
              v-model="emailTf"
              label="Correo electrónico"
              name="email"
              append-icon="mdi-email-outline"
              type="email"
              required
              :error-messages="emailRegisterErrors"
              @blur="$v.emailTf.$touch()"
            />
          </v-col>
          <v-col
            ><v-text-field
              class="icon right"
              id="passwordRegister"
              v-model="passwordTf"
              label="Contraseña"
              name="Password"
              :append-icon="icon_eye"
              @click:append="switchPassword"
              :type="showPass"
              required
              :error-messages="passwordRegisterErrors"
              @blur="$v.passwordTf.$touch()"
            />
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <div class="text-center">
            <v-btn large color="primary" @click="confirm"
              >CONFIRMAR CAMBIOS</v-btn
            >
          </div>
        </v-row>
      </v-container>
      <v-divider width="90%" class="mt-5 mb-5"> </v-divider>
      <v-container>
        <h1 class="title" style="margin-bottom:25px">
          ELIMINAR CUENTA
        </h1>
        <div class="text-center mb-4">
          <v-btn
            large
            color="primary"
            @click="
              dialogDelete = true;
              showError2 = false;
            "
            >ELIMINAR MI CUENTA</v-btn
          >
        </div>
      </v-container>
    </v-card>
    <div class="text-center">
      <v-dialog v-model="dialogConfirm" width="40%">
        <v-card class="text-center">
          <h1 class="title pt-7 pb-7">CONFIRMAR CONTRASEÑA</h1>
          <v-card-text style="font-size: 1.3em">
            Introduce tu contraseña actual para confirmar los cambios
          </v-card-text>
          <v-text-field
            class="icon"
            style="width:100% !important; padding: 0 120px 0 120px"
            v-model="passwordConfirm"
            label="Contraseña"
            name="Password"
            append-icon="mdi-lock-outline"
            type="password"
            required
          />
          <div
            v-if="showError"
            style="color: red; font-size: 1.1em"
            class="mb-5"
          >
            La contraseña no es correcta
          </div>

          <div class="text-center pt-2 pb-7">
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              large
              @click="
                dialogConfirm = false;
                passwordConfirm = '';
              "
            >
              CANCELAR
            </v-btn>
            <v-btn color="primary" large @click="update" class="ml-5">
              CONFIRMAR
            </v-btn>
          </div>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="dialogDelete" width="40%">
        <v-card class="text-center">
          <h1 class="title pt-7 pb-7">ELIMINAR MI CUENTA</h1>
          <v-card-text style="font-size: 1.3em">
            ¿Está seguro de que desea eliminar su cuenta?
          </v-card-text>
          <v-card-text style="font-size: 1.3em">
            Introduce tu contraseña actual para eliminar la cuenta
          </v-card-text>
          <v-text-field
            class="icon"
            style="width:100% !important; padding: 0 120px 0 120px"
            v-model="passwordConfirm"
            label="Contraseña"
            name="Password"
            append-icon="mdi-lock-outline"
            type="password"
            required
          />
          <div
            v-if="showError2"
            style="color: red; font-size: 1.1em"
            class="mb-5"
          >
            La contraseña no es correcta
          </div>

          <div class="text-center pt-2 pb-7">
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              large
              @click="
                dialogDelete = false;
                passwordConfirm = '';
              "
            >
              CANCELAR
            </v-btn>
            <v-btn color="primary" large @click="deleteAccount" class="ml-5">
              CONFIRMAR
            </v-btn>
          </div>
        </v-card>
      </v-dialog>
    </div>
    <v-btn
      fab
      dark
      large
      color="primary"
      fixed
      right
      bottom
      class="mb-14"
      @click="dialogHelp = true"
      style="font-size:2em"
      >?</v-btn
    >

    <Help title="OPCIONES" v-model="dialogHelp" />

  </div>
</template>

<style scoped>
.title {
  font-size: 2.2em !important;
  color: #13314a !important;
}

.table >>> tr,
.table >>> td,
.table >>> th {
  border: none !important;
  border-collapse: collapse !important;
}

.blindText {
  font-size: 1.4em !important;
  color: #13314a !important;
}

.table2 >>> td {
  padding-left: 5px !important;
}

.table {
  overflow: visible !important;
  visibility: visible !important;
}

.table >>> th {
  font-size: 1.4em !important;
  color: #13314a !important;
}

.icon {
  width: 90%;
}

.left {
  padding-left: 40px;
}

.right {
  padding-right: 40px;
}

.icon >>> .v-icon {
  font-size: 2em;
  color: #13314a;
}

.icon >>> .v-label {
  color: #13314a;
  font-size: 1.1em;
}

.icon >>> input {
  font-size: 1.2em;
}
</style>

<script>
import VSwatches from "vue-swatches";
import Help from "../components/Help";
import "vue-swatches/dist/vue-swatches.css";
import { mapState, mapMutations } from "vuex";
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  minLength,
  email,
  sameAs,
} from "vuelidate/lib/validators";

export default {
  components: { VSwatches, Help },
  mixins: [validationMixin],
  name: "Settings",
  validations: {
    nameTf: { required, maxLength: maxLength(30) },
    usernameTf: {
      required,
      maxLength: maxLength(15),
    },
    emailTf: { required, email },
    passwordTf: { required, minLength: minLength(8) },
  },
  data() {
    return {
      dialogHelp: false,
      dialogConfirm: false,
      dialogDelete: false,
      showError: false,
      showError2: false,
      colors: [
        "#f44336",
        "#e91e63",
        "#9c27b0",
        "#673ab7",
        "#3f51b5",
        "#2196f3",
        "#03a9f4",
        "#00bcd4",
        "#009688",
        "#4caf50",
        "#8bc34a",
        "#cddc39",
        "#ffeb3b",
        "#ffc107",
        "#ff9800",
        "#ff5722",
        "#795548",
        "#9e9e9e",
        "#607d8b",
        "#000000",
      ],
      colorsDefault: ["#9c27b0", "#ff9800", "#4caf50", "#2196F3"],
      colorsDeut: ["#CC2D35", "#058ED9", "#848FA2", "#2D3142"],
      colorsProt: ["#FF4242", "#A691AE", "#235FA4", "#0A284B"],
      colorsTrit: ["#9C27B0", "#1136FF", "#21B86B", "#F55F21"],
      colorOcio: "",
      colorServicios: "",
      colorCompras: "",
      colorOtro: "",
      nameTf: "",
      usernameTf: "",
      emailTf: "",
      passwordTf: "",
      passwordConfirm: "",
      showPass: "password",
    };
  },
  mounted() {
    this.colorOcio = this.colOcio;
    this.colorServicios = this.colServicios;
    this.colorCompras = this.colCompras;
    this.colorOtro = this.colOtro;
    this.nameTf = this.name;
    this.usernameTf = this.username;
    this.emailTf = this.email;
    this.passwordTf = this.password;
  },
  watch: {
    colorOcio: function() {
      this.setColOcio(this.colorOcio);
    },
    colorServicios: function() {
      this.setColServicios(this.colorServicios);
    },
    colorCompras: function() {
      this.setColCompras(this.colorCompras);
    },
    colorOtro: function() {
      this.setColOtro(this.colorOtro);
    },
  },
  computed: {
    icon_eye() {
      if (this.showPass == "password") {
        return "mdi-eye-outline";
      } else {
        return "mdi-eye-off-outline";
      }
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.nameTf.$dirty) return errors;
      !this.$v.nameTf.maxLength &&
        errors.push("El nombre debe tener como máximo 30 caracteres.");
      !this.$v.nameTf.required && errors.push("Campo requerido.");
      return errors;
    },
    usernameErrors() {
      const errors = [];
      if (!this.$v.usernameTf.$dirty) return errors;
      !this.$v.usernameTf.maxLength &&
        errors.push(
          "El nombre de usuario debe tener como máximo 15 caracteres."
        );
      !this.$v.usernameTf.required && errors.push("Campo requerido.");
      return errors;
    },
    emailRegisterErrors() {
      const errors = [];
      if (!this.$v.emailTf.$dirty) return errors;
      !this.$v.emailTf.email &&
        errors.push("El email debe tener un formato válido.");
      !this.$v.emailTf.required && errors.push("Campo requerido.");
      return errors;
    },
    passwordRegisterErrors() {
      const errors = [];
      if (!this.$v.passwordTf.$dirty) return errors;
      !this.$v.passwordTf.minLength &&
        errors.push("La contraseña debe tener como mínimo 8 caracteres.");
      !this.$v.passwordTf.required && errors.push("Campo requerido.");
      return errors;
    },
    ...mapState([
      "colOcio",
      "colServicios",
      "colCompras",
      "colOtro",
      "username",
      "name",
      "email",
      "password",
    ]),
  },
  methods: {
    switchPassword() {
      if (this.showPass == "password") {
        this.showPass = "text";
      } else {
        this.showPass = "password";
      }
    },
    setDefault() {
      this.colorOcio = this.colorsDefault[0];
      this.colorServicios = this.colorsDefault[1];
      this.colorCompras = this.colorsDefault[2];
      this.colorOtro = this.colorsDefault[3];
    },
    setDeut() {
      this.colorOcio = this.colorsDeut[0];
      this.colorServicios = this.colorsDeut[1];
      this.colorCompras = this.colorsDeut[2];
      this.colorOtro = this.colorsDeut[3];
    },
    setProt() {
      this.colorOcio = this.colorsProt[0];
      this.colorServicios = this.colorsProt[1];
      this.colorCompras = this.colorsProt[2];
      this.colorOtro = this.colorsProt[3];
    },
    setTrit() {
      this.colorOcio = this.colorsTrit[0];
      this.colorServicios = this.colorsTrit[1];
      this.colorCompras = this.colorsTrit[2];
      this.colorOtro = this.colorsTrit[3];
    },
    confirm() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        console.log("Registro incorrecto");
      } else {
        this.dialogConfirm = true;
        this.showError = false;
      }
    },
    update() {
      if (this.password === this.passwordConfirm) {
        this.setUsername(this.usernameTf);
        this.setName(this.nameTf);
        this.setPassword(this.passwordTf);
        this.setEmail(this.emailTf);
        this.passwordConfirm = "";
        this.dialogConfirm = false;
        this.showError = false;
      } else {
        this.showError = true;
      }
    },
    deleteAccount() {
      if (this.password === this.passwordConfirm) {
        this.logout();
        this.dialogDelete = false;
        this.showError2 = false;
        this.$router.push("/sesion");
      } else {
        this.showError2 = true;
      }
    },
    ...mapMutations([
      "setColOcio",
      "setColServicios",
      "setColCompras",
      "setColOtro",
      "updateProfile",
      "setUsername",
      "setName",
      "setPassword",
      "setEmail",
      "logout",
    ]),
  },
};
</script>
